import { connect } from 'react-redux';
import Gallery from '../../Components/Main/Illustrations/Gallery';
import { fetchGallery } from '../../Redux/actions/listGallery.actions';

const mapState = (state) => {
    return {
        gallery: state.listGalleryReducer.gallery,
        errorMessage: state.listGalleryReducer.errorMessage,
    };
};

const mapDispatch = (dispatch) => ({
    fetchGallery: () => {
        dispatch(fetchGallery());
    },
});

export default connect(mapState, mapDispatch)(Gallery);