export const FETCH_CREW = "FETCH_CREW";
export const FETCH_CREW_SUCCESS = "FETCH_CREW_SUCCESS";
export const FETCH_CREW_ERROR = "FETCH_CREW_ERROR";

export const fetchCrew = () => ({
    type: FETCH_CREW,
});

export const fetchCrewSuccess = (payload) => ({
    type: FETCH_CREW_SUCCESS,
    payload,
});
export const fetchCrewError = (payload) => ({
    type: FETCH_CREW_ERROR,
    payload,
});