import { connect } from 'react-redux';
import Member from '../../Components/Main/Staff/Member';
import { fetchCrew } from '../../Redux/actions/listCrew.actions';
import { fetchGallery } from '../../Redux/actions/listGallery.actions';
import { fetchBooks } from '../../Redux/actions/listBooks.actions';

const mapState = (state) => {
    return {
        crew: state.listCrewReducer.crew,
        gallery: state.listGalleryReducer.gallery,
        books: state.listBooksReducer.books,
        errorMessage: state.listCrewReducer.errorMessage,
    };
};

const mapDispatch = (dispatch) => ({
    fetchCrew: () => {
        dispatch(fetchCrew());
    },
    fetchGallery: () => {
        dispatch(fetchGallery());
    },
    fetchBooks: () => {
        dispatch(fetchBooks());
    },
});

export default connect(mapState, mapDispatch)(Member);