import { createStore, applyMiddleware, compose } from 'redux';

import rootReducer from './rootReducer';
import listTagsMiddleware from './middlewares/listTags.middleware';
import listTypesMiddleware from './middlewares/listTypes.middleware';
import listEditionsMiddleware from './middlewares/listEditions.middleware';
import listCollectionsMiddleware from './middlewares/listCollections.middleware';
import listBooksMiddleware from './middlewares/listBooks.middleware';
import listEventsMiddleware from './middlewares/listEvents.middleware';
import listGalleryMiddleware from './middlewares/listGallery.middleware';
import listGoodiesMiddleware from './middlewares/listGoodies.middleware';
import listCrewMiddleware from './middlewares/listCrew.middleware';
import userMiddleware from './middlewares/user.middleware';
import listProductsMiddleware from './middlewares/listProducts.middleware';

const composeEnhancers =
  (process.env.NODE_ENV !== 'production' &&
    typeof window !== 'undefined' &&
    window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__) ||
  compose;

const enhancers = composeEnhancers(
  applyMiddleware(
    listTagsMiddleware,
    listTypesMiddleware,
    listEditionsMiddleware,
    listCollectionsMiddleware,
    listProductsMiddleware,
    listBooksMiddleware,
    listEventsMiddleware,
    listGalleryMiddleware,
    listGoodiesMiddleware,
    listCrewMiddleware,
    userMiddleware
  )
);

const store = createStore(rootReducer, enhancers);

export default store;
