import React from 'react';

const DoublePasswordCheck = ({
  inputOnchangeValue,
  formValues,
  requiredlabel,
}) => {
  const regexSamePassword =
    formValues.password === formValues.passwordConfirm
      ? '(?=.*\\d)(?=.*[a-z])(?=.*[A-Z]).{8,}'
      : 'false';
  return (
    <>
      <div className="input-box">
        <label className="label">
          Mot de passe{requiredlabel && <span className="asterism">*</span>}
        </label>
        <input
          className="input"
          name="password"
          type="password"
          placeholder="Mot de passe"
          required
          pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}"
          title="Le mot de passe doit tre au moins de 8 caractres, contenir une majuscule et un nombre"
          value={formValues.password || ''}
          onChange={(event) => {
            inputOnchangeValue({ password: event.target.value });
          }}
        />
      </div>
      <div className="input-box">
        <label className="label">
          Confirmer{requiredlabel && <span className="asterism">*</span>}
        </label>
        <input
          className="input"
          name="passwordConfirm"
          type="password"
          placeholder="Confirmer mot de passe"
          required
          pattern={`${regexSamePassword}`}
          title="Le mot de passe doit tre identique"
          value={formValues.passwordConfirm || ''}
          onChange={(event) => {
            inputOnchangeValue({ passwordConfirm: event.target.value });
          }}
        />
      </div>
    </>
  );
};

export default DoublePasswordCheck;
