import axios from 'axios';
import {
  FETCH_EVENTS,
  fetchEventsSuccess,
  fetchEventsError,
} from '../actions/listEvents.actions';
import BASE_URL from './baseURL';

export default (store) => (next) => (action) => {
  next(action);
  switch (action.type) {
    case FETCH_EVENTS:
      axios({
        method: 'GET',
        url: `${BASE_URL}/events`,
      })
        .then((response) => {
          store.dispatch(fetchEventsSuccess(response.data));
        })
        .catch((error) => {
          store.dispatch(fetchEventsError(error));
        });
      break;
    default:
      break;
  }
};
