import { FETCH_EDITIONS_SUCCESS } from '../actions/listEditions.actions';

const INITIAL_STATE = {
  editions: [],
  isLoading: false,
  errorMessage: undefined,
};

const listEditionsReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case FETCH_EDITIONS_SUCCESS:
      return {
        ...state,
        editions: [...action.payload],
      };
    default:
      return state;
  }
};

export default listEditionsReducer;
