import axios from 'axios';
import { toast } from 'react-toastify';
import {
  LOGIN_ATTEMPT,
  loginAttempt,
  loginAttemptSuccess,
  loginAttemptError,
  FORGOTTEN_PASSWORD_SEND_MAIL,
  CHANGE_PASSWORD,
  IS_COOKIE_VALID,
  isCookieValid,
  GET_USER_DATA,
  CREATE_USER,
  getUserDataSuccess,
  getUserDataError,
  CHANGE_USER_DATA,
  changeUserDataSuccess,
  LOGOUT_ATTEMPT,
  SEND_CONTACT,
} from '../actions/user.actions';
import BASE_URL from './baseURL';

export default (store) => (next) => (action) => {
  next(action);
  switch (action.type) {
    case LOGIN_ATTEMPT:
      axios({
        method: 'POST',
        url: `${BASE_URL}/auth`,
        data: {
          email: store.getState().formReducer.email,
          password: store.getState().formReducer.password,
        },
        withCredentials: true,
      })
        .then((response) => {
          toast.info('Connexion russie, Bienvenue !');
          store.dispatch(loginAttemptSuccess(response.data));
        })
        .catch((error) => {
          error.response.status === 404 && toast.error('Mauvais Identifiants');
          error.response.status >= 500 &&
            error.response.status < 506 &&
            toast.error('Probleme de connection, ressayez plus tard');
          store.dispatch(loginAttemptError());
        });
      break;

    case FORGOTTEN_PASSWORD_SEND_MAIL:
      axios({
        method: 'POST',
        url: `${BASE_URL}/recovery/email`,
        data: {
          email: store.getState().formReducer.email,
        },
      })
        .then((response) => {
          toast.info('Un email a t envoy');
        })
        .catch((error) => {
          toast.error(
            'Un probleme est survenu, vrifiez vos identifiants ou votre connexion'
          );
        });
      break;

    case CHANGE_PASSWORD:
      axios({
        method: 'POST',
        url: `${BASE_URL}/recovery/reset`,
        data: {
          password: store.getState().formReducer.password,
          passwordConfirm: store.getState().formReducer.passwordConfirm,
          id: store.getState().formReducer.id,
          token: store.getState().formReducer.token,
        },
      })
        .then((response) => {
          toast.info('Votre mot de passe a t chang avec succs !');
        })
        .catch((error) => {
          toast.error(
            'Un probleme est survenu, vrifiez vos mots de passe ou votre connexion'
          );
        });
      break;

    case IS_COOKIE_VALID:
      axios({
        method: 'GET',
        url: `${BASE_URL}/auth/check`,
        withCredentials: true,
      })
        .then((response) => {
          response.data.token &&
            store.dispatch(loginAttemptSuccess(response.data));
          !response.data.token && store.dispatch(loginAttemptError());
        })
        .catch((error) => {
          toast.error('Une erreur est survenue, vrifiez votre connexion');
        });
      break;

    case GET_USER_DATA:
      axios({
        method: 'GET',
        url: `${BASE_URL}/account/${store.getState().userReducer.id}`,
        withCredentials: true,
      })
        .then((response) => {
          store.dispatch(getUserDataSuccess(response.data));
        })
        .catch((error) => {
          store.dispatch(getUserDataError(error.response.data));
          store.dispatch(isCookieValid());
        });
      break;

    case CHANGE_USER_DATA:
      axios({
        method: 'PUT',
        url: `${BASE_URL}/account/${store.getState().userReducer.id}`,
        withCredentials: true,
        data: { ...store.getState().formReducer },
      })
        .then((response) => {
          store.dispatch(changeUserDataSuccess(response.data));
          toast.info('Vos informations ont t mis a jour');
        })
        .catch((error) => {
          toast.error(
            'Un probleme est survenu, vrifiez votre connexion ou reconnectez vous'
          );
        });
      break;

    case CREATE_USER:
      axios({
        method: 'PUT',
        url: `${BASE_URL}/account/`,
        data: { ...store.getState().formReducer },
      })
        .then((response) => {
          toast.info(
            `Inscription ralis avec succs ${response.data.alias} ! `
          );
          store.dispatch(loginAttempt());
        })
        .catch((error) => {
          toast.error(`Une erreur est survenue : ${error.response.data}`);
        });
      break;

    case LOGOUT_ATTEMPT:
      axios({
        method: 'GET',
        url: `${BASE_URL}/auth/logout`,
        withCredentials: true,
      })
        .then((response) => {
          store.dispatch(loginAttemptError());
          toast.info('Vous vous tes deconnect avec succs');
        })
        .catch((error) => {
          toast.error(`Une erreur est survenue, ${error.response.data}`);
        });
      break;

    case SEND_CONTACT:
      axios({
        method: 'POST',
        url: `${BASE_URL}/contact`,
        data: { ...store.getState().formReducer },
      })
        .then(() => {
          toast.info('Le message a bien t envoy');
        })
        .catch((error) => {
          toast.error(`Une erreur est survenue, ${error.response.data}`);
        });
      break;

    default:
      break;
  }
};
