import { combineReducers } from 'redux';

import listProductsReducer from './reducers/listProducts.reducer';
import userReducer from './reducers/user.reducer';
import listTagsReducer from './reducers/listTags.reducer';
import listTypesReducer from './reducers/listTypes.reducer';
import listEditionsReducer from './reducers/listEditions.reducer';
import listCollectionsReducer from './reducers/listCollections.reducer';
import listBooksReducer from './reducers/listBooks.reducer';
import listEventsReducer from './reducers/listEvents.reducer';
import listGalleryReducer from './reducers/listGallery.reducer';
import listGoodiesReducer from './reducers/listGoodies.reducer';
import listCrewReducer from './reducers/listCrew.reducer';
import formReducer from './reducers/formReducer';
import filtersReducer from './reducers/filtersReducer';

export default combineReducers({
  listProductsReducer,
  userReducer,
  listTagsReducer,
  listTypesReducer,
  listEditionsReducer,
  listCollectionsReducer,
  listBooksReducer,
  listEventsReducer,
  listGalleryReducer,
  listGoodiesReducer,
  listCrewReducer,
  formReducer,
  filtersReducer,
});
