import React from 'react';

import Field from '../../../Containers/UIElement/Forms/Field/FieldContainer';
import FormContainer from '../../../Containers/UIElement/Forms/FormContainer/FormContainerContainer';
import ButtonForm from './ButtonForm/ButtonForm';

const FormUserProfil = ({ user, changeUserData }) => {
  return (
    <FormContainer onSubmitEvent={changeUserData}>
      <Field
        label="Pseudo"
        name="alias"
        type="text"
        existingvalue={user.alias}
        placeholder="Votre pseudo"
        pattern="[\w]{1,15}"
        title="Pas de caractres spciaux, et maximum 15 caractres"
      />
      <Field
        label="Prnom"
        name="firstname"
        type="text"
        existingvalue={user.firstname}
        placeholder="Votre prnom"
        pattern="[\w]{1,15}"
        title="Pas de caractres spciaux, et maximum 15 caractres"
        requiredlabel={true}
        required
      />
      <Field
        label="Nom"
        name="lastname"
        type="text"
        existingvalue={user.lastname}
        placeholder="Votre nom"
        pattern="[\w]{1,15}"
        title="Pas de caractres spciaux, et maximum 15 caractres"
        requiredlabel={true}
        required
      />
      <Field
        label="Email"
        name="email"
        type="email"
        existingvalue={user.email}
        placeholder="Email"
        requiredlabel={true}
        required
      />
      <ButtonForm type="submit">modifier</ButtonForm>
    </FormContainer>
  );
};

export default FormUserProfil;
