import { FETCH_GOODIES_SUCCESS } from '../actions/listGoodies.actions';

const INITIAL_STATE = {
    goodies: [],
    isLoading: false,
    errorMessage: undefined,
}

const listGoodiesReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case FETCH_GOODIES_SUCCESS:
            return {
                ...state,
                goodies: [...action.payload],
            };
            default:
                return state;
    }
};

export default listGoodiesReducer;