import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';

import './stylesHomepage.scss';

const Homepage = ({ fetchBooks, books, fetchEvents, events }) => {
  let randomBook = {};

  useEffect(() => {
    fetchBooks();
    fetchEvents();
  }, [fetchBooks, fetchEvents]);

  // Get a random book
  // 1) get a random id
  const totalBooks = books.length;
  function getRandomInt(max) {
    return Math.floor(Math.random() * Math.floor(max));
  }
  const randomBookIndex = getRandomInt(totalBooks);
  // 2) Gat a rambom book with the randomId
  randomBook = books[randomBookIndex];

  // Get current date
  const currentDay = new Date().toISOString();
  // Filter the events with oncoming dates only
  const nextEvents = events.filter((event) => event.start_date >= currentDay);
  // function to convert iso date in string date
  const convertToNiceDate = (dateString) => {
    const date = new Date(dateString);
    return `${date.getDate()}-${date.getMonth() + 1}-${date.getFullYear()}`;
  };

  // limiter la taille de la citation
  const getShortExcerpt = (string) => {
    const excerptArray = string.split('.');
    let newExcerpt = '';
    for (let line = 0; line < 4; line++) {
      if (line !== 19) {
        newExcerpt = newExcerpt + excerptArray[line] + '.';
      } else {
        newExcerpt = newExcerpt + excerptArray[line] + '...';
      }
    }
    return newExcerpt;
  };

  const getBreakLine = (text) => {
    const newRender = text.split(`\n`).map((paragraphe, key) => {
      return <p key={key}>{paragraphe}</p>;
    });
    return newRender;
  };

  return (
    <>
      {randomBook !== undefined && (
        <div className="excerpt">
          <blockquote>
            <div>{getBreakLine(getShortExcerpt(randomBook.excerpt))}</div>
            <p>
              <cite>{randomBook.name}</cite> -{' '}
              {randomBook.writer.map((writer) => {
                const fullname = `${writer[1]} ${writer[2]} ; `;
                return fullname;
              })}
            </p>
          </blockquote>
        </div>
      )}

      <div className="homepage">
        {events !== undefined && (
          <div className="homepage-events">
            <h3>Nos prochains vnements</h3>
            <p>
              Plus d'informations sur la page{' '}
              <Link to="/events">vnements</Link>
            </p>

            {nextEvents.map((event) => (
              <div className="homepage-event" key={event.id}>
                <h4>{event.name}</h4>
                <p>
                  {convertToNiceDate(event.start_date)} /{' '}
                  {convertToNiceDate(event.end_date)}
                </p>
                <p>{event.location}</p>
              </div>
            ))}
          </div>
        )}

        <div className="presentation">
          <h2>Qui sommes-nous ?</h2>
          <p>
            Offrir du rve et des aventures  ses lecteurs, telle est la devise
            des ditions  Loup dargent . Mais ce nest pas une maison
            ddition ordinaire, non, cest en ralit une  auto dition .
            Cr par lauteur Aurline B. pour ses propres romans ainsi que ceux
            de son amie et co-auteur, Dyane.
          </p>
          <p>
            Autrement dit, les ditions Loup dargent sont nes du dsir de deux
            auteurs de se dbrouiller seules et dtre totalement libre de
            partager leur imaginaire, sans avoir  passer par le circuit
            complexe et fastidieux des maisons dditions  standard .
          </p>

          <h2>Nous soutenir</h2>
          <p>
            Retrouvez lactualit de vos hros prfrs sur les rseaux sociaux
            !
          </p>
          <p>
            Chaque retour, chaque soutien, chaque commentaire est trs apprci
            et favorise la promotion de nos romans. Que vous ayez apprci  ou
            non  ces derniers, nous vous invitons  laisser un commentaire sur
            vos rseaux sociaux !
          </p>
          <p>
            Les auteurs indpendants et auto-dits disposent de peu de moyen
            pour se faire connatre, nous comptons donc sur vous, chers lecteurs
            et lectrices.
          </p>
          <p>Merci davance !</p>

          <h2>Service Presse</h2>
          <p>
            Vous tes journaliste, blogeurse, youtubeurse ? Vous dsirez
            recevoir l'un de nos romans afin d'en faire une chronique ? Pour
            recevoir un exemplaire numrique, il vous suffit de nous envoyer une
            demande via <Link to="/contact">le formulaire de contact</Link>
          </p>
          <p>
            Que vos avis soient bon ou mauvais, cela importe peu. Tous les gots
            sont dans la nature, aprs tout. Nanmoins, parlez de nos romans est
            important, d'autant plus que nous sommes dsireux d'emprunter un
            circuit court et de nous passer des "grosses distributions".
          </p>
          <p>Merci  vous !</p>
        </div>
      </div>
    </>
  );
};

export default Homepage;
