import React from 'react';
import { NavLink } from 'react-router-dom';

const Mobile = ({ onClick, isLogged, logoutAttempt }) => (
  <>
    <nav className="header-navigation">
      <div className="header-navigation-mobile" onClick={onClick}>
        <NavLink exact to="/" onClick={onClick}>
          Accueil
        </NavLink>
        <NavLink to="/events" onClick={onClick}>
          vnements
        </NavLink>
        <NavLink to="/books" onClick={onClick}>
          Romans
        </NavLink>
        <NavLink to="/gallery" onClick={onClick}>
          Illustrations
        </NavLink>
        <NavLink to="/goodies" onClick={onClick}>
          Produits drivs
        </NavLink>
        {isLogged && (
          <>
            <NavLink to="/profil" onClick={onClick}>
              Profil
            </NavLink>
            <NavLink to="/logout" onClick={() => logoutAttempt()}>
              Dconnexion
            </NavLink>
          </>
        )}
        {!isLogged && (
          <>
            <NavLink to="/login" onClick={onClick}>
              Connexion
            </NavLink>
            <NavLink to="/signup" onClick={onClick}>
              Inscription
            </NavLink>
          </>
        )}
      </div>
    </nav>
  </>
);

export default Mobile;
