import { connect } from 'react-redux';
import Item from '../../Components/Main/Goodies/Item';
import { fetchGoodies } from '../../Redux/actions/listGoodies.actions';

const mapState = (state) => {
    return {
        goodies: state.listGoodiesReducer.goodies,
        errorMessage: state.listGoodiesReducer.errorMessage,
    };
};

const mapDispatch = (dispatch) => ({
    fetchGoodies: () => {
        dispatch(fetchGoodies());
    },
});

export default connect(mapState, mapDispatch)(Item);