import React, { useEffect } from 'react';
import { Link, useParams } from 'react-router-dom';

import './stylesItem.scss';

const Item = ({ fetchGoodies, goodies, errorMessage }) => {
  useEffect(() => {
    fetchGoodies();
  }, [fetchGoodies]);

  const { id } = useParams();
  const itemArray = goodies.filter(item => item.id === parseInt(id, 10));

  let randomItemsArray = [];
  if (goodies.length > 0) {
    for (let index = 0; index < 4; index ++) {
      const randomId = Math.floor(Math.random() * Math.floor(goodies.length));
      if(randomId !== parseInt(id, 10)) {
        randomItemsArray.push(goodies[randomId]);
      }
    }
  }

  return (
    <div className="item-details">
      {itemArray.map((item) => (
        <div key={item.id}>
          <h2>{item.name}</h2>
          <div className="item-informations">
            <div className="item-informations-left">
              <div className="item-miniatures">
                {item.picture_file.slice(1).map((picture, index) => (
                  <img
                    key={index}
                    className="miniature"
                    src={`https://loupargent-oclock.fr/img/${picture}`}
                    alt={item.name}
                  />
                ))}
              </div>
              <img
                className="item-principale"
                src={`https://loupargent-oclock.fr/img/${item.picture_file[0]}`}
                alt={item.name}
              />
            </div>
            <div className="item-informations-right">
              <h3>Informations gnrales :</h3>
              <p>Type : {item.type_name}</p>
              <p>Collection : {item.collection_name}</p>
              <p>Format : {item.size}</p>
              <p>Prix : {item.price} </p>
            </div>
          </div>
        </div>
      ))}
      <div className="item-action-zone">
        <button>Ajouter au panier</button>
        <button>
          <Link to="/cart">Accder au panier</Link>
        </button>
      </div>
      <h3>Quelques suggestions de produits</h3>
      <div className="item-similar-products">
        {randomItemsArray.map((item, index) => (
          <Link to={`/goodie/${item.id}`} key={index}>
            <img className="item-proposal" src={`https://loupargent-oclock.fr/img/${item.picture_file}`} alt={item.name} title={item.name}/>
          </Link>
        ))}
      </div>
    </div>
  );
};

export default Item;