import React from 'react';

const EventsItemList = ({
  item,
  convertToNiceDate,
  convertToLink
}) => (
  <>
    <div className="event" key={item.id}>
      <h3>{item.name}</h3>
      <div className="event-details">
        <img
          className="event-details-poster"
          src={`https://loupargent-oclock.fr/img/${item.picture_file}`}
          alt=""
        />
        <div className="event-details-information">
          <p>{item.location}</p>
          <p>
            {convertToNiceDate(item.start_date)} /{' '}
            {convertToNiceDate(item.end_date)}
          </p>
          <p>{convertToLink(item.address)}</p>
        </div>
      </div>
    </div>
  </>
)

export default EventsItemList;