import React, { Suspense } from 'react';
import { Switch } from 'react-router-dom';

import AdminRoute from '../Containers/Pages/Routes/AdminRouteContainer';
import Loading from '../Components/UIElement/Loading/Loading';

import AdminNavigation from '../Components/Admin/AdminNavigation/AdminNavigation';

const AdminHomepage = React.lazy(() =>
  import('../Components/Admin/AdminHomepage/AdminHomepage')
);
const AdminEvents = React.lazy(() =>
  import('../Components/Admin/AdminEvents/AdminEvents')
);
const AdminCollections = React.lazy(() =>
  import('../Components/Admin/AdminCollections/AdminCollections')
);
const AdminPictures = React.lazy(() =>
  import('../Components/Admin/AdminPictures/AdminPictures')
);
const AdminUsers = React.lazy(() =>
  import('../Components/Admin/AdminUsers/AdminUsers')
);
const AdminComments = React.lazy(() =>
  import('../Components/Admin/AdminComments/AdminComments')
);
const AdminBooks = React.lazy(() =>
  import('../Containers/Admin/AdminBooksContainer')
);
const AdminProducts = React.lazy(() =>
  import('../Containers/Admin/AdminProductsContainer')
);
const AdminCommands = React.lazy(() =>
  import('../Components/Admin/AdminCommands/AdminCommands')
);
const ErrorPage = React.lazy(() =>
  import('../Components/UIElement/Error404/Error404')
);

const Admin = () => (
  <div id="admin-panel">
    <AdminNavigation />
    <Suspense fallback={<Loading />}>
      <Switch>
        <AdminRoute exact path="/admin/" component={AdminHomepage} />
        <AdminRoute exact path="/admin/events" component={AdminEvents} />
        <AdminRoute
          exact
          path="/admin/collections"
          component={AdminCollections}
        />
        <AdminRoute exact path="/admin/pictures" component={AdminPictures} />
        <AdminRoute exact path="/admin/users" component={AdminUsers} />
        <AdminRoute exact path="/admin/comments" component={AdminComments} />
        <AdminRoute exact path="/admin/books" component={AdminBooks} />
        <AdminRoute exact path="/admin/products" component={AdminProducts} />
        <AdminRoute exact path="/admin/commands" component={AdminCommands} />
        <AdminRoute path="/admin" component={ErrorPage} />
      </Switch>
    </Suspense>
  </div>
);

export default Admin;
