import { ON_CHANGE_VALUE, RESET_FILTERS } from '../actions/filters.actions';

const INITIAL_STATE = {
  edition: 0,
  collection: 0,
  type: 0,
  tags: 0,
};

const formReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case ON_CHANGE_VALUE:
      return {
        ...state,
        ...action.payload,
      };
    case RESET_FILTERS:
      return {
        edition: 0,
        collection: 0,
        type: 0,
        tags: 0,
      };
    default:
      return state;
  }
};

export default formReducer;
