import axios from 'axios';
import {
  FETCH_TAGS,
  fetchTagsSuccess,
  fetchTagsError,
} from '../actions/listTags.actions';

const BASE_URL = 'https://loupargent-oclock.fr/api';

export default (store) => (next) => (action) => {
  next(action);
  switch (action.type) {
    case FETCH_TAGS:
      axios({
        method: 'GET',
        url: `${BASE_URL}/tags`,
      })
        .then((response) => {
          store.dispatch(fetchTagsSuccess(response.data));
        })
        .catch((error) => {
          store.dispatch(fetchTagsError(error));
        });
      break;
    default:
      break;
  }
};
