import { FETCH_EVENTS_SUCCESS } from '../actions/listEvents.actions';

const INITIAL_STATE = {
    events: [],
    isLoading: false,
    errorMessage: undefined,
}

const listEventsReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case FETCH_EVENTS_SUCCESS:
            return {
                ...state,
                events: [...action.payload],
            };
        default:
            return state;
    }
};

export default listEventsReducer;