export const ERASE_FORM_STATE = 'ERASE_FORM_STATE';
export const INPUT_ONCHANGE_VALUE = 'INPUT_ONCHANGE_VALUE';
export const GET_PARAMS_FROM_URL = 'GET_PARAMS_FROM_URL';
export const GET_ID_FROM_OBJECT = 'GET_ID_FROM_OBJECT';

export const eraseFormState = () => ({
  type: ERASE_FORM_STATE,
});

export const inputOnchangeValue = (payload) => ({
  type: INPUT_ONCHANGE_VALUE,
  payload,
});

export const getParamsFromUrl = (payload) => ({
  type: GET_PARAMS_FROM_URL,
  payload,
});

export const getIdFromObject = (payload) => ({
  type: GET_ID_FROM_OBJECT,
  payload,
});
