export const FETCH_EDITIONS = 'FETCH_EDITIONS';
export const FETCH_EDITIONS_SUCCESS = 'FETCH_EDITIONS_SUCCESS';
export const FETCH_EDITIONS_ERROR = 'FETCH_EDITIONS_ERROR';

export const fetchEditions = () => ({
  type: FETCH_EDITIONS,
});

export const fetchEditionsSuccess = (payload) => ({
  type: FETCH_EDITIONS_SUCCESS,
  payload,
});

export const fetchEditionsError = (payload) => ({
  type: FETCH_EDITIONS_ERROR,
  payload,
});
