import React from 'react';

const PaginationNavigation = ({
  dataPerPage,
  totalData,
  changePage,
  currentPage,
}) => {
  const pageNumbers = [];

  for (let i = 1; i <= Math.ceil(totalData / dataPerPage); i++) {
    pageNumbers.push(i);
  }

  const isActive = (number) => (number === currentPage ? 'active' : '');

  return (
    <ul className="pagination">
      {pageNumbers.map((number) => (
        <li key={number} className={`pagination-item ${isActive(number)}`}>
          <span className="pagination-link" onClick={() => changePage(number)}>
            {number}
          </span>
        </li>
      ))}
    </ul>
  );
};

export default PaginationNavigation;
