import React, { useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import './stylesMember.scss';

const Member = ({
  fetchCrew,
  crew,
  fetchGallery,
  gallery,
  fetchBooks,
  books,
  errorMessage,
}) => {
  useEffect(() => {
    fetchCrew();
    fetchGallery();
    fetchBooks();
  }, [fetchCrew, fetchGallery, fetchBooks]);

  const { id } = useParams();

  const memberArray = crew.filter((member) => member.id === parseInt(id, 10));

  let memberRealisations = [];
  if (memberArray.length > 0) {
    if (memberArray[0].role === 'Auteur') {
      books.filter((book) =>
        book.writer.forEach((writer) => {
          if (writer[1] === memberArray[0].firstname) {
            memberRealisations.push(book);
          }
        })
      );
    }
    if (memberArray[0].role === 'Illustrateur') {
      gallery.forEach((element) => {
        if (element.firstname === memberArray[0].firstname) {
          memberRealisations.push(element);
        }
      });
    }
  }

  return (
    <>
      {memberArray.map((member) => (
        <div key={member.id}>
          <h2>
            {member.firstname} {member.lastname}, {member.role}
          </h2>
          <div className="member-informations">
            <img
              src={`https://loupargent-oclock.fr/img/${member.picture_file}`}
              alt={`${member.firstname} ${member.lastname}, ${member.role}`}
              title={`${member.firstname} ${member.lastname}, ${member.role}`}
            />
            <p>{member.biography}</p>
          </div>
          <h3>Rseaux Sociaux</h3>
          <div className="member-socials">
            <a
              className="network-social"
              href={member.facebook}
              target="_blank"
              rel="noopener noreferrer"
              title="Le site sera ouvert dans un nouvel onglet"
            >
              <FontAwesomeIcon icon={['fab', 'facebook-square']} />
            </a>
            <a
              className="network-social"
              href={member.twitter}
              target="_blank"
              rel="noopener noreferrer"
              title="Le site sera ouvert dans un nouvel onglet"
            >
              <FontAwesomeIcon icon={['fab', 'twitter-square']} />
            </a>
            <a
              className="network-social"
              href={member.instagram}
              target="_blank"
              rel="noopener noreferrer"
              title="Le site sera ouvert dans un nouvel onglet"
            >
              <FontAwesomeIcon icon={['fab', 'instagram-square']} />
            </a>
            <a
              className="network-social"
              href={member.linkedin}
              target="_blank"
              rel="noopener noreferrer"
              title="Le site sera ouvert dans un nouvel onglet"
            >
              <FontAwesomeIcon icon={['fab', 'linkedin']} />
            </a>
          </div>
          {memberRealisations.length > 0 && (
            <div className="member-works">
              <h3>Ralisations</h3>
              {memberRealisations.map((realisation) => {
                if (typeof realisation.picture_file === `object`) {
                  return (
                    <Link to={`/book/${realisation.id}`} key={realisation.id}>
                      <img
                        src={`https://loupargent-oclock.fr/img/${realisation.picture_file[0]}`}
                        alt={realisation.name}
                        title={realisation.name}
                      />
                    </Link>
                  );
                } else {
                  return (
                    <Link to="/gallery" key={realisation.id}>
                      <img
                        src={`https://loupargent-oclock.fr/img/${realisation.picture_file}`}
                        alt={realisation.title}
                        title={realisation.title}
                      />
                    </Link>
                  );
                }
              })}
            </div>
          )}
        </div>
      ))}
    </>
  );
};

export default Member;
