import React from 'react';

import './stylesFilters.scss';

const Filters = ({
  editions = undefined,
  collections = undefined,
  types = undefined,
  tags = undefined,
  reducerCollection,
  reducerType,
  onChangeValue,
  resetFilters,
}) => (
  <div className="filters">
    {editions !== undefined && (
      <select className="filters-input" name="edtions"></select>
    )}
    {collections !== undefined && (
      <select
        className="filters-input"
        name="collections"
        value={reducerCollection}
        onChange={(event) => {
          onChangeValue({ collection: +event.target.value });
        }}
      >
        <option value={0}>Collections</option>
        {collections.map((item) => (
          <option key={item.id} value={item.id}>
            {item.label.slice(0, 20)}...
          </option>
        ))}
      </select>
    )}
    {types !== undefined && (
      <select
        className="filters-input"
        name="types"
        value={reducerType}
        onChange={(event) => {
          onChangeValue({ type: +event.target.value });
        }}
      >
        <option value={0}>Types</option>
        {types.map((item) => (
          <option key={item.id} value={item.id}>
            {item.label}
          </option>
        ))}
      </select>
    )}
    {tags !== undefined && (
      <select className="filters-input" name="tags"></select>
    )}
    <button className="button-filters" onClick={() => resetFilters()}>
      rinitialiser
    </button>
  </div>
);

export default Filters;
