import React from 'react';
import { Route, Redirect } from 'react-router-dom';

function PrivateRoute({
  component: Component,
  isLogged,
  isCookieLoading,
  ...rest
}) {
  return (
    <>
      {isCookieLoading || (
        <Route
          {...rest}
          render={(props) =>
            isLogged ? <Component {...props} /> : <Redirect to="/login" />
          }
        />
      )}
    </>
  );
}

export default PrivateRoute;
