import React, { useState } from 'react';
import axios from 'axios';
import { toast } from 'react-toastify';

import ButtonForm from './ButtonForm/ButtonForm';

const FormUserAvatar = ({ id, getUserData }) => {
  const [fileToUpload, setFileToUpload] = useState(null);
  const data = new FormData();
  data.append('picture_file', fileToUpload);
  const onSubmitFile = () => {
    toast.warning('Photo en cours de transfert...');
    axios({
      method: 'PUT',
      url: `https://loupargent-oclock.fr/api/account/${id}`,
      data,
    })
      .then((response) => {
        toast.info('La photo de profil a t mise  jour');
        getUserData(id);
      })
      .catch((error) => {
        toast.error(`Une erreur est survenue : ${error.message}`);
      });
  };
  return (
    <form
      className="form"
      onSubmit={(event) => {
        event.preventDefault();
        onSubmitFile();
      }}
    >
      <div className="input-box">
        <label className="label">Image Avatar</label>
        <input
          type="file"
          accept="image/*"
          name="picture_file"
          encType="multipart/form-data"
          className="input"
          onChange={(event) => {
            setFileToUpload(event.target.files[0]);
          }}
        />
      </div>
      <ButtonForm type="submit">envoyer</ButtonForm>
    </form>
  );
};

export default FormUserAvatar;
