import { connect } from 'react-redux';
import FormUserChangePassword from '../../../Components/UIElement/Forms/FormUserChangePassword';
import { inputOnchangeValue } from '../../../Redux/actions/form.actions';
import { changeUserData } from '../../../Redux/actions/user.actions';

const mapState = (state) => ({
  form: state.formReducer,
});

const mapDispatch = (dispatch) => ({
  changeUserData: () => {
    dispatch(changeUserData());
  },
  inputOnchangeValue: (payload) => {
    dispatch(inputOnchangeValue(payload));
  },
});

export default connect(mapState, mapDispatch)(FormUserChangePassword);
