import { connect } from 'react-redux';
import App from '../App';
import { isCookieValid } from '../Redux/actions/user.actions';

const mapState = (state) => {
  return {
    isCookieLoading: state.userReducer.isCookieLoading,
  };
};

const mapDispatch = (dispatch) => ({
  isCookieValid: () => {
    dispatch(isCookieValid());
  },
});

export default connect(mapState, mapDispatch)(App);
