import { connect } from 'react-redux';
import Books from '../../Components/Main/Books/Books';
import { fetchBooks } from '../../Redux/actions/listBooks.actions';
import { fetchTags } from '../../Redux/actions/listTags.actions';
import { fetchEditions } from '../../Redux/actions/listEditions.actions';
import { fetchCollections } from '../../Redux/actions/listCollections.actions';

const mapState = (state) => {
  return {
    books: state.listBooksReducer.books,
    tags: state.listTagsReducer.tags,
    collections: state.listCollectionsReducer.collections,
    editions: state.listEditionsReducer.editions,
    errorMessage: state.listBooksReducer.errorMessage,
  };
};

const mapDispatch = (dispatch) => ({
  fetchBooks: () => {
    dispatch(fetchBooks());
  },
  fetchTags: () => {
    dispatch(fetchTags());
  },
  fetchCollections: () => {
    dispatch(fetchCollections());
  },
  fetchEditions: () => {
    dispatch(fetchEditions());
  },
});

export default connect(mapState, mapDispatch)(Books);
