import { connect } from 'react-redux';
import Events from '../../Components/Main/Events/Events';
import { fetchEvents } from '../../Redux/actions/listEvents.actions';

const mapState = (state) => {
    return {
        events: state.listEventsReducer.events,
        errorMessage: state.listEventsReducer.errorMessage,
    };
};

const mapDispatch = (dispatch) => ({
    fetchEvents: () => {
        dispatch(fetchEvents());
    },
});

export default connect(mapState, mapDispatch)(Events);