import axios from 'axios';
import {
  FETCH_GALLERY,
  fetchGallerySuccess,
  fetchGalleryError,
} from '../actions/listGallery.actions';
import BASE_URL from './baseURL';

export default (store) => (next) => (action) => {
  next(action);
  switch (action.type) {
    case FETCH_GALLERY:
      axios({
        method: 'GET',
        url: `${BASE_URL}/gallery`,
      })
        .then((response) => {
          store.dispatch(fetchGallerySuccess(response.data));
        })
        .catch((error) => {
          store.dispatch(fetchGalleryError(error));
        });
      break;
    default:
      break;
  }
};
