import React from 'react';

import './stylesField.scss';

const Field = ({
  inputOnchangeValue,
  formValues,
  existingvalue,
  requiredlabel,
  label,
  ...props
}) => {
  const inputValue =
    formValues[props.name] !== undefined
      ? formValues[props.name]
      : existingvalue;
  return (
    <div className="input-box">
      <label className="label">
        {label}
        {requiredlabel && <span className="asterism">*</span>}
      </label>
      <input
        {...props}
        className="input"
        value={inputValue || ''}
        onChange={(event) => {
          let value = event.target.value;
          if (props.type === 'number') {
            value = +event.target.value;
          }
          inputOnchangeValue({ [props.name]: value });
        }}
      />
    </div>
  );
};

export default Field;
