import {
  ERASE_FORM_STATE,
  INPUT_ONCHANGE_VALUE,
  GET_PARAMS_FROM_URL,
  GET_ID_FROM_OBJECT,
} from '../actions/form.actions';

const INITIAL_STATE = {};

const formReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case ERASE_FORM_STATE:
      return {};
    case INPUT_ONCHANGE_VALUE:
      return {
        ...state,
        ...action.payload,
      };
    case GET_PARAMS_FROM_URL:
      return {
        ...state,
        ...action.payload,
      };
    case GET_ID_FROM_OBJECT:
      return {
        ...state,
        ...action.payload,
      };
    default:
      return state;
  }
};

export default formReducer;
