import { connect } from 'react-redux';
import Novel from '../../Components/Main/Books/Novel';
import { fetchBooks } from '../../Redux/actions/listBooks.actions';

const mapState = (state) => {
  return {
    books: state.listBooksReducer.books,
    errorMessage: state.listBooksReducer.errorMessage,
  };
};

const mapDispatch = (dispatch) => ({
  fetchBooks: () => {
    dispatch(fetchBooks());
  },
});

export default connect(mapState, mapDispatch)(Novel);
