import { FETCH_GALLERY_SUCCESS } from '../actions/listGallery.actions';

const INITIAL_STATE = {
    gallery: [],
    isLoading: false,
    errorMessage: undefined,
}

const listGalleryReducer = (state = INITIAL_STATE, action) => {
    switch(action.type) {
        case FETCH_GALLERY_SUCCESS:
            return {
                ...state,
                gallery: [...action.payload],
            };
        default:
            return state;
    }
};

export default listGalleryReducer;