import React from 'react';

const GalleryItemList = ({ item, toTrue, setIdIllustration }) => {
  return (
    <>
      <div className="picture-miniature" key={item.id}>
        <img
          className="picture-visuel"
          src={`https://loupargent-oclock.fr/img/${item.picture_file}`}
          alt="Miniature de l'item"
          onClick={() => {
            toTrue();
            setIdIllustration(item.id);
          }}
        />
      </div>
    </>
  );
};

export default GalleryItemList;
