import React, { useEffect } from 'react';
import Loading from '../../UIElement/Loading/Loading';

import EventsItemList from './EventsItemList';
import Pagination from '../../UIElement/Pagination/PaginationContainer';

import './stylesEvents.scss';

const Events = ({ fetchEvents, events, errorMessage }) => {
  useEffect(() => {
    fetchEvents();
  }, [fetchEvents]);

  const convertToNiceDate = (dateString) => {
    const date = new Date(dateString);
    return `${date.getDate()}-${date.getMonth() + 1}-${date.getFullYear()}`;
  };

  const convertToLink = (address) => {
    const addressToCheck = address;
    if (addressToCheck.includes('http')) {
      return (
        <a
          href={addressToCheck}
          target="_blank"
          title="Un nouvel onglet sera ouvert"
          rel="noopener noreferrer"
        >
          Lien Discord
        </a>
      );
    } else {
      return addressToCheck;
    }
  };

  if (events.length === 0) {
    return <Loading />;
  } else {
    return (
      <>
        <h1>vnements</h1>
        <div className="events">
          <Pagination
            data={events}
            Component={EventsItemList}
            convertToNiceDate={convertToNiceDate}
            convertToLink={convertToLink}
          />
        </div>
      </>
    );
  }
};

export default Events;
