import { connect } from 'react-redux';
import NoAuthRoute from '../../../Pages/Routes/NoAuthRoute';

const mapState = (state) => {
  return {
    isLogged: state.userReducer.isLogged,
    isCookieLoading: state.userReducer.isCookieLoading,
  };
};

const mapDispatch = null;

export default connect(mapState, mapDispatch)(NoAuthRoute);
