import React from 'react';

import './stylesField.scss';

const TextArea = ({
  label,
  formValues,
  existingvalue,
  inputOnchangeValue,
  requiredlabel,
  ...props
}) => {
  const inputValue =
    formValues[props.name] !== undefined
      ? formValues[props.name]
      : existingvalue;
  return (
    <div className="input-box text-area">
      <label className="label text-area">
        {label}
        {requiredlabel && <span className="asterism">*</span>}
      </label>
      <textarea
        className="form-textarea"
        value={inputValue || ''}
        {...props}
        onChange={(event) => {
          inputOnchangeValue({ [props.name]: event.target.value });
        }}
      />
    </div>
  );
};

export default TextArea;
