export const FETCH_GOODIES = "FETCH_GOODIES";
export const FETCH_GOODIES_SUCCESS = "FETCH_GOODIES_SUCCESS";
export const FETCH_GOODIES_ERROR = "FETCH_GOODIES_ERROR";

export const fetchGoodies = () => ({
    type: FETCH_GOODIES,
});

export const fetchGoodiesSuccess = (payload) => ({
    type: FETCH_GOODIES_SUCCESS,
    payload,
});

export const fetchGoodiesError = (payload) => ({
    type: FETCH_GOODIES_ERROR,
    payload,
});