import React, { useEffect } from 'react';

import './stylesFormContainer.scss';

const FormContainer = ({
  children,
  className,
  onSubmitEvent,
  eraseFormState,
}) => {
  useEffect(() => {
    eraseFormState();
  }, [eraseFormState]);
  return (
    <form
      className={`form ${className}`}
      onSubmit={(e) => {
        e.preventDefault();
        onSubmitEvent();
      }}
    >
      <p style={{ color: '#c2c5cc' }}>
        (<span className="asterism">*</span>) <i>champs obligatoires</i>
      </p>
      {children}
    </form>
  );
};

export default FormContainer;
