import { connect } from 'react-redux';
import Filters from '../../../Components/UIElement/Filters/Filters';
import {
  onChangeValue,
  resetFilters,
} from '../../../Redux/actions/filters.actions';

const mapState = (state) => ({
  reducerEdition: state.filtersReducer.edition,
  reducerCollection: state.filtersReducer.collection,
  reducerType: state.filtersReducer.type,
  reducerTags: state.filtersReducer.tags,
});

const mapDispatch = (dispatch) => ({
  onChangeValue: (payload) => {
    dispatch(onChangeValue(payload));
  },
  resetFilters: () => {
    dispatch(resetFilters());
  },
});

export default connect(mapState, mapDispatch)(Filters);
