import { connect } from 'react-redux';
import Homepage from '../../Components/Main/Homepage/Homepage';
import { fetchBooks } from '../../Redux/actions/listBooks.actions';
import { fetchEvents } from '../../Redux/actions/listEvents.actions';


const mapState = (state) => {
  return {
    books: state.listBooksReducer.books,
    events: state.listEventsReducer.events,
    errorMessageBooks: state.listBooksReducer.errorMessage,
    errorMessageEvents: state.listEventsReducer.errorMessage,
  };
};

const mapDispatch = (dispatch) => ({
  fetchBooks: () => {
    dispatch(fetchBooks());
  },
  fetchEvents: () => {
    dispatch(fetchEvents());
  }
});

export default connect(mapState, mapDispatch)(Homepage);
