import {
  IS_COOKIE_VALID,
  LOGIN_ATTEMPT_SUCCESS,
  LOGIN_ATTEMPT_ERROR,
  GET_USER_DATA_SUCCESS,
  CHANGE_USER_DATA_SUCCESS,
} from '../actions/user.actions';

const INITIAL_STATE = {
  id: '',
  email: '',
  role: '',
  isLogged: false,
  isCookieLoading: true,
};

const userReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case IS_COOKIE_VALID:
      return {
        ...state,
      };
    case LOGIN_ATTEMPT_SUCCESS:
      return {
        ...state,
        id: action.payload.id,
        email: action.payload.email,
        role: action.payload.role,
        isLogged: true,
        isCookieLoading: false,
      };
    case LOGIN_ATTEMPT_ERROR:
      return {
        id: '',
        email: '',
        role: '',
        isLogged: false,
        isCookieLoading: false,
      };

    case GET_USER_DATA_SUCCESS:
      return {
        ...state,
        ...action.payload,
      };

    case CHANGE_USER_DATA_SUCCESS:
      return {
        ...state,
        ...action.payload,
      };
    default:
      return state;
  }
};

export default userReducer;
