import React, { useState } from 'react';
import { useMediaQuery } from 'react-responsive';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import Mobile from './Mobile';
import Desktop from './Desktop';

import Background from '../../../assets/img/bckgGalaxy.jpg';
import Banner from '../../../assets/img/banner.png';
import './stylesNavigation.scss';

const Navigation = ({ isLogged, logoutAttempt }) => {
  const [showNavMobile, setShowNavMobile] = useState(false);

  const isMobile = useMediaQuery({
    query: '(max-device-width: 899px)',
  });
  const isDesktop = useMediaQuery({
    query: '(min-device-width: 900px)',
  });

  return (
    <header
      className="header"
      style={{ backgroundImage: `url(${Background})` }}
    >
      <div className="header-opacity">
        <div className="header-title">
          {isMobile && (
            <>
              <p className="burger">
                <FontAwesomeIcon
                  icon="bars"
                  onClick={() => setShowNavMobile(true)}
                />
              </p>
              {showNavMobile && (
                <Mobile
                  onClick={() => setShowNavMobile(false)}
                  isLogged={isLogged}
                  logoutAttempt={logoutAttempt}
                />
              )}
            </>
          )}
          <img
            className="banner"
            src={Banner}
            alt="Logo des ditions Loup d'Argent"
          />
        </div>
        {isDesktop && (
          <Desktop isLogged={isLogged} logoutAttempt={logoutAttempt} />
        )}
      </div>
    </header>
  );
};

export default Navigation;
