import React, { useEffect } from 'react';
import { Switch, Route } from 'react-router-dom';
import { library } from '@fortawesome/fontawesome-svg-core';
import {
  faPen,
  faTrashAlt,
  faQuoteRight,
  faQuoteLeft,
  faFileImage,
  faPlusCircle,
  faMinusCircle,
  faBars,
} from '@fortawesome/free-solid-svg-icons';
import {
  faFacebookSquare,
  faTwitterSquare,
  faInstagramSquare,
  faLinkedin,
} from '@fortawesome/free-brands-svg-icons/';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import Main from './Pages/Main';
import Admin from './Pages/Admin';
import Loading from './Components/UIElement/Loading/Loading';

import './App.scss';

library.add(
  faFacebookSquare,
  faTwitterSquare,
  faInstagramSquare,
  faLinkedin,
  faPen,
  faTrashAlt,
  faQuoteRight,
  faQuoteLeft,
  faFileImage,
  faPlusCircle,
  faMinusCircle,
  faBars,
);

function App({ isCookieValid, isCookieLoading }) {
  useEffect(() => {
    isCookieValid();
  }, [isCookieValid]);
  return (
    <div className="App">
      <ToastContainer
        position="top-right"
        autoClose={4000}
        hideProgressBar={true}
        newestOnTop={true}
        closeOnClick
        rtl={false}
        draggable
        pauseOnHover
      />
      {isCookieLoading ? (
        <Loading />
      ) : (
        <Switch>
          <Route path="/admin">
            <Admin />
          </Route>
          <Route path="/">
            <Main />
          </Route>
        </Switch>
      )}
    </div>
  );
}

export default App;
