export const filters = ({ state = [], edition, collection, tag, type }) => {
  const byCollection =
    collection !== undefined && collection !== 0
      ? state.filter((item) => item.id_collection === collection)
      : state;

  const byType =
    type !== undefined && type !== 0
      ? byCollection.filter((item) => item.id_type === type)
      : byCollection;

  return byType;
};
