import {
  FETCH_PRODUCTS_SUCCESS,
  FETCH_ALL_ADMIN_BOOKS_SUCCESS,
  FETCH_ALL_ADMIN_PRODUCTS_SUCCESS,
} from '../actions/listProducts.actions';

const INITIAL_STATE = {
  products: [],
  editions: [],
  collections: [],
  tags: [],
  users: [],
  types: [],
  isLoading: false,
  errorMessage: undefined,
};

const listProductsReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case FETCH_PRODUCTS_SUCCESS:
      return {
        ...state,
        products: [...action.payload],
      };
    case FETCH_ALL_ADMIN_BOOKS_SUCCESS:
      return {
        ...state,
        products: [...action.payload[0].data],
        tags: [...action.payload[1].data],
        editions: [...action.payload[2].data],
        collections: [...action.payload[3].data],
        users: [...action.payload[4].data],
        types: [...action.payload[5].data],
      };
    case FETCH_ALL_ADMIN_PRODUCTS_SUCCESS:
      return {
        ...state,
        products: [...action.payload[0].data],
        collections: [...action.payload[1].data],
        users: [...action.payload[2].data],
        types: [...action.payload[3].data],
      };
    default:
      return state;
  }
};

export default listProductsReducer;
