import React, { Suspense } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';

import AuthenticateRoute from '../Containers/Pages/Routes/AuthenticateRouteContainer';
import NoAuthRoute from '../Containers/Pages/Routes/NoAuthRouteContainer';
import Loading from '../Components/UIElement/Loading/Loading';

import Navigation from '../Containers/Main/NavigationContainer';

import Homepage from '../Containers/Main/HomepageContainer';
import Events from '../Containers/Main/EventsContainer';
import Books from '../Containers/Main/BooksContainer';
import Novel from '../Containers/Main/NovelContainer';
import Gallery from '../Containers/Main/GalleryContainer';
import Goodies from '../Containers/Main/GoodiesContainer';
import Item from '../Containers/Main/ItemContainer';
import Staff from '../Containers/Main/CrewContainer';
import Member from '../Containers/Main/MemberContainer';
import Profil from '../Containers/Main/ProfilContainer';
import Footer from '../Components/Main/Footer/Footer';
import Cart from '../Components/Main/Cart/Cart';

const Login = React.lazy(() => import('../Components/Main/Login/Login'));
const SignUp = React.lazy(() => import('../Components/Main/SignUp/SignUp'));
const Cgu = React.lazy(() => import('../Components/Main/CG/Cgu'));
const Cgv = React.lazy(() => import('../Components/Main/CG/Cgv'));
const Copyright = React.lazy(() => import('../Components/Main/CG/Copyright'));
const Legal = React.lazy(() => import('../Components/Main/CG/Legal'));
const Contact = React.lazy(() => import('../Components/Main/Contact/Contact'));
const Error404 = React.lazy(() =>
  import('../Components/UIElement/Error404/Error404')
);
const SendMailForgottenPassword = React.lazy(() =>
  import('../Components/Main/Recovery/SendMailForgottenPassword')
);
const ChangePasswordForgottenPassword = React.lazy(() =>
  import('../Components/Main/Recovery/ChangePasswordForgottenPassword')
);

const Main = () => (
  <>
    <Navigation />
    <main>
      <Suspense fallback={<Loading />}>
        <Switch>
          <Route exact path="/">
            <Homepage />
          </Route>
          <Route path="/events">
            <Events />
          </Route>
          <Route exact path="/books" component={Books} />
          <Route path="/book/:id" component={Novel} />
          <Route path="/gallery">
            <Gallery />
          </Route>
          <Route exact path="/goodies" component={Goodies} />
          <Route path="/goodie/:id" component={Item} />
          <NoAuthRoute path="/login" component={Login} />
          <NoAuthRoute path="/signup" component={SignUp} />
          <Route path="/mentions-legales">
            <Legal />
          </Route>
          <Route path="/copyright">
            <Copyright />
          </Route>
          <Route path="/conditions-generales-utilisation">
            <Cgu />
          </Route>
          <Route path="/conditions-generales-vente">
            <Cgv />
          </Route>
          <Route path="/staff" component={Staff} />
          <Route path="/member/:id" component={Member} />
          <AuthenticateRoute path="/profil" component={Profil} />
          <Route path="/contact">
            <Contact />
          </Route>
          <Route path="/cart">
            <Cart />
          </Route>
          <Route exact path="/recovery" component={SendMailForgottenPassword} />
          <Route
            path="/recovery/:id/:token"
            component={ChangePasswordForgottenPassword}
          />
          <Route exact path="/logout">
            <Redirect exact to="/" />
          </Route>
          <Route path="/">
            <Error404 />
          </Route>
        </Switch>
      </Suspense>
    </main>
    <Footer />
  </>
);

export default Main;
