import React from 'react';
import { v4 as uuidv4 } from 'uuid';

import './stylesField.scss';

const Select = ({
  label,
  type,
  options = {},
  formValues,
  existingvalue,
  inputOnchangeValue,
  requiredlabel,
  ...props
}) => {
  const inputValue =
    formValues[props.name] !== undefined
      ? formValues[props.name]
      : existingvalue;
  return (
    <div className="input-box">
      <label className="label">
        {label}
        {requiredlabel && <span className="asterism">*</span>}
      </label>
      <select
        type={type}
        className="input"
        value={inputValue || { value: 0 }}
        onChange={(event) => {
          inputOnchangeValue({
            [props.name]: event.target.value,
          });
        }}
        {...props}
      >
        <option value={0}>Choisir</option>
        {options.map((option) => (
          <option value={option.id} key={uuidv4()}>
            {option.label ||
              option.name ||
              option.alias ||
              `${option.city} - ${option.postal} - ${option.adress}`}
          </option>
        ))}
      </select>
    </div>
  );
};

export default Select;
