import React, { useState } from 'react';
import { NavLink, Link } from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import './stylesAdminNavigation.scss';

const AdminNavigation = () => {
  const [showNavMobile, setShowNavMobile] = useState(false);

  const isMobile = useMediaQuery({
    query: '(max-device-width: 899px)',
  });
  const isDesktop = useMediaQuery({
    query: '(min-device-width: 900px)',
  });
  return (
    <>
      {isMobile && (
        <>
          <p className="burger-admin">
            <FontAwesomeIcon
              icon="bars"
              onClick={() => {
                setShowNavMobile(true);
              }}
            />
          </p>
          {showNavMobile && (
            <nav
              id="admin-navigation_mobile"
              onClick={() => {
                setShowNavMobile(false);
              }}
            >
              <Link to="/">ELA</Link>
              <NavLink exact to="/admin/">
                Accueil
              </NavLink>
              <NavLink to="/admin/events">Evenements</NavLink>
              <NavLink to="/admin/collections">Collections</NavLink>
              <NavLink to="/admin/pictures">Illustrations</NavLink>
              <NavLink to="/admin/users">Membres</NavLink>
              <NavLink to="/admin/comments">Commentaires</NavLink>
              <NavLink to="/admin/books">Livres</NavLink>
              <NavLink to="/admin/products">Produits</NavLink>
              <NavLink to="/admin/commands">Commandes</NavLink>
            </nav>
          )}
        </>
      )}
      {isDesktop && (
        <nav id="admin-navigation">
          <Link to="/">ELA</Link>
          <NavLink exact to="/admin/">
            Accueil
          </NavLink>
          <NavLink to="/admin/events">Evenements</NavLink>
          <NavLink to="/admin/collections">Collections</NavLink>
          <NavLink to="/admin/pictures">Illustrations</NavLink>
          <NavLink to="/admin/users">Membres</NavLink>
          <NavLink to="/admin/comments">Commentaires</NavLink>
          <NavLink to="/admin/books">Livres</NavLink>
          <NavLink to="/admin/products">Produits</NavLink>
          <NavLink to="/admin/commands">Commandes</NavLink>
        </nav>
      )}
    </>
  );
};

export default AdminNavigation;
