import axios from 'axios';
import {
  FETCH_TYPES,
  fetchTypesSuccess,
  fetchTypesError,
} from '../actions/listTypes.actions';

const BASE_URL = 'https://loupargent-oclock.fr/api';

export default (store) => (next) => (action) => {
  next(action);
  switch (action.type) {
    case FETCH_TYPES:
      axios({
        method: 'GET',
        url: `${BASE_URL}/types`,
      })
        .then((response) => {
          store.dispatch(fetchTypesSuccess(response.data));
        })
        .catch((error) => {
          store.dispatch(fetchTypesError(error));
        });
      break;
    default:
      break;
  }
};
