import React from 'react';
import { Route, Redirect } from 'react-router-dom';

function PrivateRoute({
  component: Component,
  role,
  isCookieLoading,
  ...rest
}) {
  return (
    <>
      {isCookieLoading || (
        <Route
          {...rest}
          render={(props) =>
            role === 'Editeur' ? (
              <Component {...props} />
            ) : (
              <Redirect to="/404" />
            )
          }
        />
      )}
    </>
  );
}

export default PrivateRoute;
