import { FETCH_TYPES_SUCCESS } from '../actions/listTypes.actions';

const INITIAL_STATE = {
  types: [],
  isLoading: false,
  errorMessage: undefined,
};

const listTypesReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case FETCH_TYPES_SUCCESS:
      return {
        ...state,
        types: [...action.payload],
      };
    default:
      return state;
  }
};

export default listTypesReducer;
