import axios from 'axios';
import {
  FETCH_BOOKS,
  fetchBooksSuccess,
  fetchBooksError,
} from '../actions/listBooks.actions';
import BASE_URL from './baseURL';

export default (store) => (next) => (action) => {
  next(action);
  switch (action.type) {
    case FETCH_BOOKS:
      axios({
        method: 'GET',
        url: `${BASE_URL}/books`,
      })
        .then((response) => {
          store.dispatch(fetchBooksSuccess(response.data));
        })
        .catch((error) => {
          store.dispatch(fetchBooksError(error));
        });
      break;
    default:
      break;
  }
};
