import { FETCH_BOOKS_SUCCESS } from '../actions/listBooks.actions';

const INITIAL_STATE = {
  books: [],
  isLoading: false,
  errorMessage: undefined,
};

const listBooksReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case FETCH_BOOKS_SUCCESS:
      return {
        ...state,
        books: [...action.payload],
      };
    default:
      return state;
  }
};

export default listBooksReducer;
