import React, { useEffect } from 'react';

import GoodiesItemList from './GoodiesItemList';
import Pagination from '../../UIElement/Pagination/PaginationContainer';
import Filters from '../../../Containers/UIElement/Filters/FiltersContainer';

import './stylesGoodies.scss';

const Goodies = ({
  fetchGoodies,
  goodies,
  fetchTypes,
  types,
  fetchCollections,
  collections,
}) => {
  useEffect(() => {
    fetchGoodies();
    fetchTypes();
    fetchCollections();
  }, [fetchGoodies, fetchTypes, fetchCollections]);

  const getCollectionPicture = (itemIdCollection) => {
    const collection = collections.find(
      (collection) => collection.id === itemIdCollection
    );

    if (collection !== undefined) {
      return `https://loupargent-oclock.fr/img/${collection.picture_file}`;
    }
  };

  const getCollectionName = (itemIdCollection) => {
    const collection = collections.find(
      (collection) => collection.id === itemIdCollection
    );

    if (collection !== undefined) {
      return collection.label;
    }
  };

  const getTypeName = (itemIdType) => {
    const type = types.find((type) => type.id === itemIdType);

    if (type !== undefined) {
      return type.label;
    }
  };
  return (
    <div>
      <h1>Produits drivs</h1>
      <Filters types={types} collections={collections} />

      {goodies.length !== 0 ? (
        <div className="goodies">
          <Pagination
            data={goodies}
            Component={GoodiesItemList}
            getCollectionName={getCollectionName}
            getCollectionPicture={getCollectionPicture}
            getTypeName={getTypeName}
          />
        </div>
      ) : (
        <div>Pas de produits correspondants</div>
      )}
    </div>
  );
};

export default Goodies;
