import axios from 'axios';
import {
  FETCH_CREW,
  fetchCrewSuccess,
  fetchCrewError,
} from '../actions/listCrew.actions';
import BASE_URL from './baseURL';

export default (store) => (next) => (action) => {
  next(action);
  switch (action.type) {
    case FETCH_CREW:
      axios({
        method: 'GET',
        url: `${BASE_URL}/crew`,
      })
        .then((response) => {
          store.dispatch(fetchCrewSuccess(response.data));
        })
        .catch((error) => {
          store.dispatch(fetchCrewError(error));
        });
      break;
    default:
      break;
  }
};
