import React from 'react';
import { Link } from 'react-router-dom';

const BooksItemList = ({
  item,
  getEditionName,
  getEditionPicture
}) => (
  <>
    <div className="novel" key={item.id}>
      <div className="novel-header">
        <Link to={`/book/${item.id}`}>
          <h3 className="novel-title">{item.name}</h3>
        </Link>
      </div>
      <div className="novel-main">
        <img
          className="novel-main-picture"
          src={`https://loupargent-oclock.fr/img/${item.picture_file[0]}`}
          alt={item.name}
        />
        <img
          className="novel-gender"
          src={getEditionPicture(item.id_edition)}
          alt={getEditionName(item.id_edition)}
          title={getEditionName(item.id_edition)}
        />
      </div>
      <div className="novel-alt">
        {item.tag.map((oneTag) => (
          <img
            key={oneTag[0]}
            className="novel-alt-picture"
            src={`https://loupargent-oclock.fr/img/${oneTag[2]}`}
            alt={oneTag[1]}
            title={oneTag[1]}
          />
        ))}
      </div>
    </div>
  </>
)

export default BooksItemList;