import { FETCH_COLLECTIONS_SUCCESS } from '../actions/listCollections.actions';

const INITIAL_STATE = {
  collections: [],
  isLoading: false,
  errorMessage: undefined,
};

const listCollectionsReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case FETCH_COLLECTIONS_SUCCESS:
      return {
        ...state,
        collections: [...action.payload],
      };
    default:
      return state;
  }
};

export default listCollectionsReducer;
