import { connect } from 'react-redux';
import AuthenticateRoute from '../../../Pages/Routes/AuthenticateRoute';

const mapState = (state) => {
  return {
    isLogged: state.userReducer.isLogged,
    isCookieLoading: state.userReducer.isCookieLoading,
  };
};

const mapDispatch = null;

export default connect(mapState, mapDispatch)(AuthenticateRoute);
