import axios from 'axios';
import { toast } from 'react-toastify';
import {
  FETCH_PRODUCTS,
  fetchProductsSuccess,
  FETCH_ALL_ADMIN_BOOKS,
  fetchAllAdminBooks,
  fetchAllAdminBooksSuccess,
  FETCH_ALL_ADMIN_PRODUCTS,
  fetchAllAdminProducts,
  fetchAllAdminProductsSuccess,
  CREATE_OR_MODIFY_BOOK,
  CREATE_OR_MODIFY_PRODUCT,
  DELETE_PRODUCT,
  DELETE_BOOK,
} from '../actions/listProducts.actions';
import { isCookieValid } from '../actions/user.actions';
import BASE_URL from './baseURL';

axios.defaults.withCredentials = true;

export default (store) => (next) => (action) => {
  next(action);
  switch (action.type) {
    case FETCH_PRODUCTS:
      axios({
        method: 'GET',
        url: `${BASE_URL}/books`,
      })
        .then((response) => {
          store.dispatch(fetchProductsSuccess(response.data));
        })
        .catch((error) => {
          toast.error('Une erreur est survenue, vrifiez votre connexion');
        });
      break;

    case FETCH_ALL_ADMIN_BOOKS:
      axios
        .all([
          axios.get(`${BASE_URL}/books`),
          axios.get(`${BASE_URL}/tags`),
          axios.get(`${BASE_URL}/editions`),
          axios.get(`${BASE_URL}/collections`),
          axios.get(`${BASE_URL}/admin/users`),
          axios.get(`${BASE_URL}/types`),
        ])
        .then(
          axios.spread((...responses) => {
            store.dispatch(fetchAllAdminBooksSuccess(responses));
          })
        )
        .catch((error) => {
          error.response.status === 501 && isCookieValid();
        });
      break;

    case FETCH_ALL_ADMIN_PRODUCTS:
      axios
        .all([
          axios.get(`${BASE_URL}/goodies`),
          axios.get(`${BASE_URL}/collections`),
          axios.get(`${BASE_URL}/admin/users`),
          axios.get(`${BASE_URL}/types`),
        ])
        .then(
          axios.spread((...responses) => {
            store.dispatch(fetchAllAdminProductsSuccess(responses));
          })
        )
        .catch((error) => {
          error.response.status === 501 && isCookieValid();
        });
      break;

    case CREATE_OR_MODIFY_BOOK:
      const urlModifiedBook = store.getState().formReducer.id
        ? `${BASE_URL}/admin/products/${store.getState().formReducer.id}`
        : `${BASE_URL}/admin/products/`;
      axios({
        method: 'PUT',
        url: urlModifiedBook,
        data: { ...store.getState().formReducer },
      })
        .then(() => {
          store.dispatch(fetchAllAdminBooks());
          toast.info('Produit enregistr');
        })
        .catch((error) => {
          error.response.status === 501 && isCookieValid();
          console.log(error);
        });
      break;

    case CREATE_OR_MODIFY_PRODUCT:
      const urlModifiedProduct = store.getState().formReducer.id
        ? `${BASE_URL}/admin/products/${store.getState().formReducer.id}`
        : `${BASE_URL}/admin/products/`;
      axios({
        method: 'PUT',
        url: urlModifiedProduct,
        data: { ...store.getState().formReducer },
      })
        .then(() => {
          store.dispatch(fetchAllAdminProducts());
          toast.info('Produit enregistr');
        })
        .catch((error) => {
          error.response.status === 501 && isCookieValid();
          console.log(error);
        });
      break;

    case DELETE_PRODUCT:
      axios({
        method: 'DELETE',
        url: `${BASE_URL}/admin/products/${store.getState().formReducer.id}`,
      })
        .then(() => {
          toast.info('Produit supprim avec succs');
          store.dispatch(fetchAllAdminProducts());
        })
        .catch(() => {
          toast.error('Une erreur est survenue, ressayez plus tard');
        });
      break;

    case DELETE_BOOK:
      axios({
        method: 'DELETE',
        url: `${BASE_URL}/admin/products/${store.getState().formReducer.id}`,
      })
        .then(() => {
          toast.info('Produit supprim avec succs');
          store.dispatch(fetchAllAdminBooks());
        })
        .catch(() => {
          toast.error('Une erreur est survenue, ressayez plus tard');
        });
      break;

    default:
      break;
  }
};
