export const IS_LOGGED = 'IS_LOGGED';
export const LOGIN_ATTEMPT = 'LOGIN_ATTEMPT';
export const LOGIN_ATTEMPT_SUCCESS = 'LOGIN_ATTEMPT_SUCCESS';
export const LOGIN_ATTEMPT_ERROR = 'LOGIN_ATTEMPT_ERROR';
export const FORGOTTEN_PASSWORD_SEND_MAIL = 'FORGOTTEN_PASSWORD_SEND_MAIL';
export const CHANGE_PASSWORD = 'CHANGE_PASSWORD';
export const IS_COOKIE_VALID = 'IS_COOKIE_VALID';
export const CHANGE_USER_DATA = 'CHANGE_USER_DATA';
export const CHANGE_USER_DATA_SUCCESS = 'CHANGE_USER_DATA_SUCCESS';
export const CREATE_USER = 'CREATE_USER';
export const GET_USER_DATA = 'GET_USER_DATA';
export const GET_USER_DATA_SUCCESS = 'GET_USER_DATA_SUCCESS';
export const GET_USER_DATA_ERROR = 'GET_USER_DATA_ERROR';
export const LOGOUT_ATTEMPT = 'LOGOUT_ATTEMPT';
export const SEND_CONTACT = 'SEND_CONTACT';

export const isLogged = () => ({
  type: IS_LOGGED,
});

export const loginAttempt = () => ({
  type: LOGIN_ATTEMPT,
});

export const loginAttemptSuccess = (payload) => ({
  type: LOGIN_ATTEMPT_SUCCESS,
  payload,
});

export const loginAttemptError = () => ({
  type: LOGIN_ATTEMPT_ERROR,
});

export const forgottenPasswordSendMail = () => ({
  type: FORGOTTEN_PASSWORD_SEND_MAIL,
});

export const changePassword = () => ({
  type: CHANGE_PASSWORD,
});

export const isCookieValid = () => ({
  type: IS_COOKIE_VALID,
});

export const changeUserData = () => ({
  type: CHANGE_USER_DATA,
});

export const changeUserDataSuccess = (payload) => ({
  type: CHANGE_USER_DATA_SUCCESS,
  payload,
});

export const createUser = () => ({
  type: CREATE_USER,
});

export const getUserData = () => ({
  type: GET_USER_DATA,
});

export const getUserDataSuccess = (payload) => ({
  type: GET_USER_DATA_SUCCESS,
  payload,
});

export const getUserDataError = (payload) => ({
  type: GET_USER_DATA_ERROR,
  payload,
});

export const logoutAttempt = () => ({
  type: LOGOUT_ATTEMPT,
});

export const sendContact = () => ({
  type: SEND_CONTACT,
});
