import { FETCH_CREW_SUCCESS } from '../actions/listCrew.actions';

const INITIAL_STATE = {
    crew : [],
    isLoading: false,
    errorMessage: undefined,
}

const listCrewReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case FETCH_CREW_SUCCESS:
            return {
                ...state,
                crew: [...action.payload],
            };
        default:
            return state;
    }
};

export default listCrewReducer;