import React, { useEffect } from 'react';
import Loading from '../../UIElement/Loading/Loading';

import BooksItemList from './BooksItemList';
import Pagination from '../../UIElement/Pagination/PaginationContainer';

import './stylesBooks.scss';

const Books = ({
  fetchBooks,
  books,
  fetchTags,
  tags,
  fetchEditions,
  editions,
  fetchCollections,
  collections,
  errorMessage,
}) => {
  useEffect(() => {
    fetchBooks();
    fetchTags();
    fetchEditions();
    fetchCollections();
  }, [fetchBooks, fetchTags, fetchEditions, fetchCollections]);

  const getEditionPicture = (bookIdEdition) => {

  const edition = editions.find(edition => edition.id === bookIdEdition);

    if (edition !== undefined) {
      return `https://loupargent-oclock.fr/img/${edition.picture_file}`;
    }
  };

  const getEditionName = (bookIdEdition) => {
    
  const edition = editions.find(edition => edition.id === bookIdEdition);


    if (edition !== undefined) {
      return edition.name;
    }
  };

  if (books.length === 0) {
    return <Loading />;
  } else {
    return (
      <div>
        <h1>Romans</h1>
        <div className="novels">
          <Pagination
            data={books}
            Component={BooksItemList}
            getEditionName={getEditionName}
            getEditionPicture={getEditionPicture}
          />
        </div>
      </div>
    );
  }
};

export default Books;
