import { FETCH_TAGS_SUCCESS } from '../actions/listTags.actions';

const INITIAL_STATE = {
  tags: [],
  isLoading: false,
  errorMessage: undefined,
};

const listTagsReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case FETCH_TAGS_SUCCESS:
      return {
        ...state,
        tags: [...action.payload],
      };
    default:
      return state;
  }
};

export default listTagsReducer;
