import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';

import './stylesStaff.scss';

const Staff = ({ fetchCrew, crew, errorMessage }) => {

  useEffect(() => {
    fetchCrew();
  }, [fetchCrew]);

  return (
    <>
    <h2>Les membres de l'quipe ditoriale</h2>
    <div className="staff-listing">
      {crew.map((member) => (
        <div className="staff" key={member.id}>
          <Link to={`/member/${member.id}`}><img className="staff-selfie" src={`https://loupargent-oclock.fr/img/${member.picture_file}`} alt={`${member.role}`} title={`${member.firstname} ${member.lastname}, ${member.role}`}/></Link>
          <h3>{member.firstname}, {member.role}</h3>
        </div>
      ))}
    </div>
    </>
  )
}

export default Staff;