import axios from 'axios';
import {
  FETCH_EDITIONS,
  fetchEditionsSuccess,
  fetchEditionsError,
} from '../actions/listEditions.actions';

const BASE_URL = 'https://loupargent-oclock.fr/api';

export default (store) => (next) => (action) => {
  next(action);
  switch (action.type) {
    case FETCH_EDITIONS:
      axios({
        method: 'GET',
        url: `${BASE_URL}/editions`,
      })
        .then((response) => {
          store.dispatch(fetchEditionsSuccess(response.data));
        })
        .catch((error) => {
          store.dispatch(fetchEditionsError(error));
        });
      break;
    default:
      break;
  }
};
