import { connect } from 'react-redux';
import Staff from '../../Components/Main/Staff/Staff';
import { fetchCrew } from '../../Redux/actions/listCrew.actions';

const mapState = (state) => {
    return {
        crew: state.listCrewReducer.crew,
        errorMessage: state.listCrewReducer.errorMessage,
    };
};

const mapDispatch = (dispatch) => ({
    fetchCrew: () => {
        dispatch(fetchCrew());
    },
});

export default connect(mapState, mapDispatch)(Staff);