import axios from 'axios';
import {
  FETCH_COLLECTIONS,
  fetchCollectionsSuccess,
  fetchCollectionsError,
} from '../actions/listCollections.actions';

const BASE_URL = 'https://loupargent-oclock.fr/api';

export default (store) => (next) => (action) => {
  next(action);
  switch (action.type) {
    case FETCH_COLLECTIONS:
      axios({
        method: 'GET',
        url: `${BASE_URL}/collections`,
      })
        .then((response) => {
          store.dispatch(fetchCollectionsSuccess(response.data));
        })
        .catch((error) => {
          store.dispatch(fetchCollectionsError(error));
        });
      break;
    default:
      break;
  }
};
