import axios from 'axios';
import {
  FETCH_GOODIES,
  fetchGoodiesSuccess,
  fetchGoodiesError,
} from '../actions/listGoodies.actions';
import BASE_URL from './baseURL';

export default (store) => (next) => (action) => {
  next(action);
  switch (action.type) {
    case FETCH_GOODIES:
      axios({
        method: 'GET',
        url: `${BASE_URL}/goodies`,
      })
        .then((response) => {
          store.dispatch(fetchGoodiesSuccess(response.data));
        })
        .catch((error) => {
          store.dispatch(fetchGoodiesError(error));
        });
      break;
    default:
      break;
  }
};
