import React from 'react';
import { Link } from 'react-router-dom';

const GoodiesItemList = ({
  item,
  getCollectionName,
  getCollectionPicture,
  getTypeName,
}) => (
  <div className="goodie" key={item.id}>
    <div className="goodie-main">
      <Link to={`/goodie/${item.id}`}>
        <img
          className="goodie-main-picture"
          src={`https://loupargent-oclock.fr/img/${item.picture_file}`}
          alt={item.name}
          title={item.name}
        />
      </Link>
      <p className="goodie-alt-type">{getTypeName(item.id_type)}</p>
      <img
        className="goodie-alt-picture"
        src={getCollectionPicture(item.id_collection)}
        alt={getCollectionName(item.id_collection)}
        title={`Collection ${getCollectionName(item.id_collection)}`}
      />
    </div>
  </div>
);

export default GoodiesItemList;
